.header-bgr{
  background-color: $colorViolet !important;
  .header-bgr {
    .content-header{
      .header_title{
        color: #ffffff;
        font-family: NanumSquare;
        font-size: 18px;
        font-weight: 800;
      }
      .header-right{
        .svg-logout{
          color: #fff;
        }
      }
    }
  }
}
.dashboard-02p-dp-01 {
  background-color: $colorViolet;
}
.content-dashboard-head{
  .content-dashboard-head-row{
    .content-dashboard-head__username{
      color: #ffffff;
      font-size: 18px;
      margin: 8px 3px 24px 22px;
      font-family: NanumSquare;
      font-weight: 800;
      span{
        margin: 12px 229px 28px 3px;
        opacity: 0.8;
        font-family: NanumSquare;
        font-size: 11px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
      }
    }
  }
  .content-total-sales{
    &__text{
      .text{
        margin: 24px 12px 5px 20px;
        font-family: NanumSquare;
        font-size: 14px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
      }
      
    }
    &__price{
      margin: 0 30px 0 21px;
      font-family: Roboto;
      font-size: 40px;
      font-weight: 900;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #ffffff;
      display: flex;
      &__currency_unit{
        margin: 13px 0 13px 3px;
        font-family: NanumSquare;
        font-size: 20px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #ffffff;
      }
    }
  }
  .box{
    .box-child{
      display: flex;
      flex-wrap: wrap;
      margin: 10px 20px 39px;
      padding: 2px 20px;
      height: 55px;
      border-radius: 5px;
      background-color: #692dae !important;
      div {
        label{
          color: #ffffff !important;
          margin: 0 8px 3px 0;
          // opacity: 0.8;
          font-family: NanumSquare;
          font-size: 12px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: left;
          
        }
        span.price{
          // margin: 3px 87px 0 0;
          font-family: Roboto;
          font-size: 16px;
          font-weight: bold;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          // text-align: left;
          color: #ffffff;
        }
      }
      &__store{
          // text-align: right;
          padding: 0 0 0 40px;
      }
      &__total-merchant{
          // text-align: right;
          padding: 0 0 0 40px;
      }
    }
  }
}

.content-dashboard{
  background-color: #ffffff;
  border-radius: 30px 30px 0 0;
  min-height: 90vh;
  // position: fixed;
  width: 100%;
  &__branches{
    .branches-title{
      margin: 20px 0px 0px 20px;
      font-family: NanumSquare;
      font-size: 14px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #111111;
    }
    span{
      margin: 1px 98px 17px 3px;
      font-family: Roboto;
      font-size: 11px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #111111;
    }
    
  }

  &__chart-real-time{
    .chart-real-time-title{
      margin: 20px 0px 0px 20px;
      font-family: NanumSquare;
      font-size: 14px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #111111;
    }
    span{
      margin: 1px 98px 17px 3px;
      font-family: Roboto;
      font-size: 11px;
      font-weight: 300;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #111111;
    }
    
  }
  &__hot-selling-items{
    .title{
      margin: 20px 0px 0px 20px;
      font-family: NanumSquare;
      font-size: 14px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #111111;
    }
    .hot-selling-items-right{
      .filter_dropdown{
        display: flex;
        padding: 15px 30px 0px 15px;
        justify-content: flex-end;
        .dropdown{
          button {
            outline   : none;
            box-shadow: none;
            padding   : 5px 5px;
            font-size : 11px;
            &:focus {
                outline: none;
            }
        }
        }
      }
    }
    .selling-items-child{
      margin: 20px 22px;
      .number-selling{
        label{
          margin: 13px 10px 13px 5px;
          font-family: Roboto;
          font-size: 18px;
          font-weight: 300;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: left;
          color: #111111;
        }
        
      }
      .title-items{
        font-family: NanumSquare;
        font-size: 14px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #111111;
      }
      .cases{
        // margin: 3px 158px 10px 10px;
        font-family: NanumSquare;
        font-size: 11px;
        font-weight: normal;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #111111;
      }
      .price-items{
        margin: 0 0 0 2px;
        font-family: NanumSquare;
        font-size: 16px;
        font-weight: bold;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: right;
        color: #111111;
      }
      .box-price{
        text-align: right;
      }
    }
    
  }
  .product-preference-chart{
    .title-preference-chart{
      margin: 20px 0px 0px 20px;
      font-family: NanumSquare;
      font-size: 14px;
      font-weight: 800;
      font-stretch: normal;
      font-style: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #111111;
    }
  }
  &__slide{
    margin: 0px 0px 0px 20px;
    .slide-box{
      width: 273px !important;
      height: 104px;
      margin: 15px 10px 20px 0;
      padding: 20px 15px;
      border-radius: 10px;
      background-color: rgb(234 223 246 / 66%);
      .slide-title-text{
        // margin: 0 70px 10px 0;
        font-family: NanumSquare;
        font-size: 14px;
        font-weight: 800;
        font-stretch: normal;
        font-style: normal;
        line-height: normal;
        letter-spacing: normal;
        text-align: left;
        color: #111111;
      }
      .slide-price-box{
        padding: 0px;
      }
      .slide-title{
        &__sales{
          opacity: 0.8;
          font-family: NanumSquare;
          font-size: 12px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: left;
          color: #111111;
        }
      }
      .slide-price{
        text-align: right;
        padding-right: 10px;
        &__sales{
          font-family: NanumSquare;
          font-size: 19px;
          font-weight: 800;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: left;
          color: #111111;
        }
        .price-child{
          // opacity: 0.8;
          font-family: Roboto;
          font-size: 12px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: left;
          color: #111111;
        }
        .curency{
          opacity: 0.8;
          font-family: NanumSquare;
          font-size: 11px;
          font-weight: normal;
          font-stretch: normal;
          font-style: normal;
          line-height: normal;
          letter-spacing: normal;
          text-align: left;
          color: #111111;
        }
      }
    }
  }
}
#chartPreference{
  width: 100%;
  height: 200px;
  // padding-bottom: 20px;
}